@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: "Noto Sans JP", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  background-color: aqua;
}

@media only screen and (max-width: 480px) {
  .App {
    overflow: scroll;
  }
  svg {
    width: 80vw;
    height: 80vw;
    overflow: hidden;
  }

  .nav-brand {
    font-size: xx-large;
  }

  .pallete-item {
    width: 8rem !important;
    height: 8rem !important;
    border: 4px solid white;
  }
}

h1 {
  font-size: xx-large;
}

h2 {
  font-size: x-large;
}

h3 {
  font-size: larger;
}

h4 {
  font-size: large;
}

h5 {
  font-size: medium;
}

h6 {
  font-size: small;
}

.App {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-content: stretch;
          align-content: stretch;
  -webkit-align-items: stretch;
          align-items: stretch;
  grid-gap: 2rem;
  gap: 2rem;
}

.App > * {
  margin: 0px !important;
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 8vh;
  font-size: xx-large;
}
.nav-brand {
  letter-spacing: 0.2rem;
}

section.header-strip {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-content: center;
          align-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

section.header-strip > * {
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
  -webkit-align-self: center;
          align-self: center;
  justify-self: center;
}

section.svg {
  -webkit-flex: 0 0 auto !important;
          flex: 0 0 auto !important;
  overflow: hidden;
  -webkit-align-self: center;
          align-self: center;
  justify-self: flex-start;
}

section.svg > * {
  mix-blend-mode: luminosity;
  max-width: 480px;
  margin: 0px;
}

section.controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: stretch;
          align-items: stretch;
  text-align: start;
  padding: 1rem;
  grid-gap: 2rem;
  gap: 2rem;
}

section.controls > * {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.tag {
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
}

.colorpicker {
  -webkit-flex: 2 2 auto !important;
          flex: 2 2 auto !important;
}

.color-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* flex-flow: row wrap; */
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: stretch;
          align-content: stretch;
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
}

.color-controls > * {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.save {
  -webkit-flex: 0 1 auto !important;
          flex: 0 1 auto !important;
}

section.palette {
  margin: 0px !important;
  align-self: stretch;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: stretch;
          align-content: stretch;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.pallete-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.palette-item-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: stretch;
          align-items: stretch;
  border-radius: 20px;
  overflow: hidden;
  background-color: ivory;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  text-align: center !important;
  position: relative;
}

.palette-controls-container {
  display: none;
  position: absolute;
}
.palette-controls-container > * {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}
.pallete-colors {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  border-radius: inherit;
  width: 12rem;
  min-height: 18rem;
  letter-spacing: 2px;
}

.pallete-colors > * {
  -webkit-align-items: center !important;
          align-items: center !important;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  align-items: center;
}

.pallete-item-primary {
  grid-area: primary;
  -webkit-flex: 6 1 !important;
          flex: 6 1 !important;
}

.pallete-item-secondary {
  grid-area: secondary;
  -webkit-flex: 3 1 auto !important;
          flex: 3 1 auto !important;
}

.pallete-item-accent {
  grid-area: accent;
  -webkit-flex: 1 1 auto !important;
          flex: 1 1 auto !important;
}

footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 1rem;
}

input[type="text"] {
  padding: 1rem;
  font-size: large;
  border-radius: 2rem;
  outline: none;
}

button {
  border-radius: 2rem;
  padding: 0.8rem;
  font-size: large;
  border: none;
}

button:hover {
  -webkit-filter: opacity(0.85);
          filter: opacity(0.85);
}

.circle {
  border-radius: 100%;
  min-width: 1.5rem;
  min-height: 1.5rem;
}

.no-border {
  border: 2px solid transparent !important;
}

